.cards {
    &__wrap {
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin: 48px 0 50px; } }

.swiper {
    width: 100%;
    &-pagination {
        display: flex;
        justify-content: center;
        margin-top: 40px;
        &-bullet {
            display: block;
            margin: 0 20px;
            width: 295px;
            padding-bottom: 15.3%;
            border: 1px solid #68b8ff00;
            opacity: 1;
            border-radius: 13px;
            background-repeat: no-repeat;
            background-position: center -250px;
            background-size: cover;
            transition: .4s;
            opacity: 0.4;
            &:nth-child(1) {
                background-image: url("../../public/img/cards/card1.jpg");
                background-position: center top; }
            &:nth-child(2) {
                background-image: url("../../public/img/cards/card1.jpg");
                background-position: center bottom; }
            &:nth-child(3) {
                background-image: url("../../public/img/cards/card2.jpg");
                background-position: center top; }
            &:nth-child(4) {
                background-image: url("../../public/img/cards/card2.jpg");
                background-position: center bottom; }
            &-active {
                opacity: 1; } } }
    &-wrapper {
        height: auto; } }
.card {
    width: 66%;
    margin: 0 auto;
    &__link {
        color: #CFAC6A;
        display: flex;
        align-items: center;
        img {
            margin-right: 15px; } }
    &__image {
        border-radius: 13px;
        background-repeat: no-repeat;
        background-position: center -250px;
        background-size: cover;
        height: 67vh;
        min-height: 500px;
        max-height: 784px;
        &_one {
            background-image: url("../../public/img/cards/card1.jpg");
