 }    //         height: auto

@media screen and (max-width: 600px) {
    .arrows {
        display: none; }
    .swiper {
        &-pagination {
            position: absolute;
            bottom: 24px;
            left: 0;
            right: 0;
            margin: 0 auto;
            z-index: 2;
            &-bullet, .investment &-bullet {
                width: 8px;
                height: 8px;
                border-radius: 50px;
                padding-bottom: 0;
                margin: 0 4px;
                opacity: .7;
                @for $var from 1 through 5 {
                    &:nth-child(#{$var}), .investment &:nth-child(#{$var}) {
                        background: #fff; } }
                &-active {
                    width: 24px;
                    opacity: 1; } } } }
    .card {
        width: 100%;
        &__image {
            height: 320px;
            min-height: 320px; } } }

@media screen and (max-width: 500px) {
    .card {
        width: 100%;
        &__image {
            height: 250px;
            min-height: 250px; } } }

@media screen and (max-width: 380px) {
    .card {
        width: 100%;
        &__image {
            height: 210px;
            min-height: 210px; } } }

@media screen and (max-width: 330px) {
    .card {
        width: 100%;
        &__image {
            height: 180px;
            min-height: 180px; } } }
